html,
body {
    background-color: #f5f8fa;
}
body {
    position: relative;
    font-family: 'Roboto', sans-serif;
    font-size: 1em;
    line-height: 1.6em;
}

/* Cards */
.card {
    &-footer {
        font-size: .8em;
        line-height: 1.4em;
    }
}

/* Filters */
.table-check {
    .title {
        &:nth-child(3) {
            border-top: 1px solid #dee2e6;
        }
    }
}
@media (min-width: 992px) {
    .title {
        &:nth-child(3) {
            border-top: 0!important;
            border-left: 1px solid #dee2e6!important;
        }
    }
}

/* Table */
.table>:not(:last-child)>:last-child>* {
    border-bottom-color: var(--bs-table-bg);;
}
.table {
    border-collapse: collapse;
}
.table tbody td {
    border-bottom: 0;
}

.table thead th {
    vertical-align: middle;
    line-height: 1.4em;
}
.table tfoot th {
    padding-top: .7rem;
    line-height: 1em;
}
.table-hover>tbody>tr:hover {
    background-color: #dedede;
}
.table .border-start {
    border-left-color: #373b3e !important;
}
table {
    border-spacing: 0 !important;
    border-collapse: separate !important;
}

.bg {
    &-pfizer-biontech {
        background-color: #007cc2;
    }
    &-moderna {
        background-color: #e51937;
    }
    &-oxford-astrazeneca {
        background-color: #0f1f47;
    }
    &-sputnik-v {
        background-color: #002b89;
    }
    &-covaxin {
        background-color: #02b2e1;
    }
    &-sinovac-butantan {
        background-color: #ed5b06;
    }
    &-johnson-johnson {
        background-color: #d51900;
    }
}

/* Sorter */
.tablesorter-bootstrap {
    width: 100%;
}
.tablesorter-bootstrap thead .tablesorter-header {
    background-position: right .6rem center;
    background-repeat: no-repeat;
    cursor: pointer;
    white-space: normal;
}
.tablesorter-bootstrap thead .sorter-false {
    cursor: default;
    background-image: none;
}
.tablesorter-bootstrap .tablesorter-header-inner {
    position: relative;
    padding-right: 1.4rem;
}
@media (min-width: 992px) {
    .tablesorter-bootstrap .text-lg-center .tablesorter-header-inner {
        padding-left: 1.4rem;
    }
}
